import React from "react";
import { useEffect } from "react";
import styled from "styled-components";
import { auth, provider } from "../Firebase";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import {
  selectUserName,
  selectUserPhoto,
  selectUserEmail,
  setUserLoginDetails,
  setSignOutState,
} from "../features/users/userSlice";
const Header = (props) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const username = useSelector(selectUserName);
  const userPhoto = useSelector(selectUserPhoto);
  useEffect(() => {
    auth.onAuthStateChanged(async (user) => {
      if (user) {
        setUser(user);
        history.push("/home");
      }
    });
  }, [username]);
  const handleAuth = () => {
    if (!username) {
      auth
        .signInWithPopup(provider)
        .then((result) => {
          console.log("this is reult");
          console.log(result);
          setUser(result.user);
        })
        .catch((error) => {
          alert(error.message);
        });
    } else if (username) {
      auth.signOut().then(() => {
        dispatch(setSignOutState());
        history.push("/");
      });
    }
  };
  const setUser = (user) => {
    dispatch(
      setUserLoginDetails({
        name: user.displayName,
        email: user.email,
        photo: user.photoURL,
      })
    );
  };

  return (
    <Nav>
      <Logo>
        <img src='/images/logo.svg' />
      </Logo>
      {!username ? (
        <Loginn onClick={handleAuth}>login</Loginn>
      ) : (
        <>
          <Navmenu>
            <a href='/home'>
              <img src='/images/home.svg' alt='HOME' />
              <span>HOME</span>
            </a>
            <a>
              <img src='/images/serch.svg' alt='SEARCH' />
              <span>SEARCH</span>
            </a>
            <a>
              <img src='/images/watchlist.svg' alt='WATCHLIST' />
              <span>WATCHLIST</span>
            </a>
            <a>
              <img src='/images/original.svg' alt='ORIGINALS' />
              <span>ORIGINALS</span>
            </a>
            <a>
              <img src='/images/movies.svg' alt='MOVIES' />
              <span>MOVIES</span>
            </a>
            <a>
              <img src='/images/series.svg' alt='SERIES' />
              <span>SERIES</span>
            </a>
          </Navmenu>
          <Signout>
            <Userimage src={userPhoto} />
            <Dropdown>
              <span onClick={handleAuth}>Signout</span>
            </Dropdown>
          </Signout>
        </>
      )}
    </Nav>
  );
};
const Nav = styled.nav`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 70px;
  background-color: #090b13;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 36px;
  letter-spacing: 16px;
  zindex: 9;
`;
const Logo = styled.a`
  padding: 0;
  width: 80px;
  margin-top: 4px;
  max-height: 70px;
  font-size: 0;
  display: inline-block;
  img {
    display: block;
    width: 100%;
  }
`;
const Navmenu = styled.div`
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  height: 100%;
  justify-content: flex-end;
  margin: 0px;
  padding: 0px;
  position: relative;
  margin-right: auto;
  margin-left: 25px;
  a {
    display: flex;
    align-items: center;
    padding: 0 12px;
    img {
      height: 20px;
      min-width: 20px;
      width: 20px;
      z-index: auto;
    }
    span {
      color: rgb(249, 249, 249);
      font-size: 13px;
      letter-spacing: 1.42px;
      line-height: 1.08;
      padding: 2px 0px;
      white-space: nowrap;
      position: relative;
      &:before {
        background-color: rgb(249, 249, 249);
        border-radius: 0px 0px 4px 4px;
        bottom: -6px;
        content: "";
        height: 2px;
        left: 0px;
        opacity: 0;
        position: absolute;
        right: 0px;
        transform-origin: left center;
        transform: scaleX(0);
        transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
        visibility: hidden;
        width: auto;
      }
    }
    &:hover {
      span:before {
        transform: scaleX(1);
        visibility: visible;
        opacity: 1 !important;
      }
    }
  }
  /* @media (max-width: 768px) {
  display: none;
} */
`;
const Loginn = styled.a`
  background-color: rgba(0, 0, 0, 0.6);
  padding: 8px 16px;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  border: 1px solid #f9f9f9;
  border-radius: 4px;
  transition: all 0.2s ease 0s;
  &:hover {
    background-color: #f9f9f9;
    color: #000;
    border-color: transparent;
  }
`;
const Userimage = styled.img`
  height: 100%;
`;

const Dropdown = styled.div`
  position: absolute;
  top: 48px;
  right: 0px;
  background: rgb(19, 19, 19);
  border: 1px solid white;
  border-radius: 4px;
  padding:10px
  font-size:14px;
  letter-spacing:3px;
  width:100px;
  opacity:0;
`;
const Signout = styled.div`
  position: relative;
  display: flex;
  height: 48px;
  width: 48px;
  cursor: pointer;
  align-item: center;
  justify-content: center;
  ${Userimage} {
    border-radius: 50px;
    width: 100%;
    height: 100%;
  }

  &:hover {
    ${Dropdown} {
      opacity: 1;
      transition-duration: 1s;
    }
  }
`;
export default Header;
